import React, { useState } from 'react';
import { Form, Modal, Button, Row, Col, Input, message } from 'antd';

import { addCategory, saveCategory } from './../../services';

const FormItem = Form.Item;

const CategoryFormCmp = props => {
  const {
    messages,
    visible,
    hideModal,
    category,
    fetchCategories,
    form: { getFieldDecorator },
    form,
  } = props;

  const [loading, setLoading] = useState(false);

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 18 },
    },
  };

  const handleReset = () => {
    form.resetFields();
  };

  const submit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          name: values.name,
        };

        try {
          setLoading(true);
          if (category && category.id) {
            form.id = category.id;
            await saveCategory(form);
          } else {
            await addCategory(form);
          }

          fetchCategories();
          handleReset();
          hideModal();
          setLoading(false);
          message.success(messages.default.success);
        } catch (error) {
          setLoading(false);
          message.error(error.message);
        }
      }
    });
  };

  return (
    <Modal
      centered
      width={600}
      title={
        category && category.name ? messages.subscription.editCat : messages.subscription.addCat
      }
      visible={visible}
      onOk={hideModal}
      onCancel={hideModal}
      footer={[
        <Button key="back" onClick={hideModal}>
          {messages.default.cancel}
        </Button>,
        <Button key="submit" type="primary" loading={loading} onClick={submit}>
          {category && category.name ? messages.default.save : messages.default.create}
        </Button>,
      ]}
    >
      <Form {...formItemLayout} onSubmit={submit} autocomplete="off">
        <Row justify="center" type="flex">
          <Col xs={24}>
            <FormItem label={messages.subscription.name} hasFeedback>
              {getFieldDecorator('name', {
                initialValue: category && category.name ? category.name : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.name.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

const CategoryForm = Form.create()(CategoryFormCmp);

export { CategoryForm };
