import React, { useState } from 'react';
import { Form, Modal, Button, Row, Col, Input, message, InputNumber, Select, Switch } from 'antd';

import { addSuscription, saveSuscription } from './../../services';

const FormItem = Form.Item;
const { Option } = Select;

const SubscriptionsFormCmp = props => {
  const {
    messages,
    categories,
    visible,
    hideModal,
    suscription,
    fetchSuscriptions,
    form: { getFieldDecorator },
    form,
  } = props;

  const [loading, setLoading] = useState(false);

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
  };
  const formItemLayoutTitle = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  const handleReset = () => {
    form.resetFields();
  };

  const submit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          title: values.title,
          CategoryId: values.CategoryId,
          pages: values.pages,
          freePages: values.freePages,
          price: values.price,
          minPrice: values.minPrice,
          pricePerAdditionalPage: values.pricePerAdditionalPage,
          period: values.period,
          freeDays: values.freeDays,
          maxSharedEngines: values.maxSharedEngines,
          maxPrivateInstances: values.maxPrivateInstances,
          maxUsers: values.maxUsers,
          maxClones: values.maxClones,
          canUsePGB: values.canUsePGB || suscription.canUsePGB || false,
          canTrain: values.canTrain || suscription.canTrain || false,
          canUseApi: values.canUseApi || suscription.canUseApi || false,
        };

        try {
          setLoading(true);
          if (suscription && suscription.id) {
            form.id = suscription.id;
            await saveSuscription(form);
          } else {
            await addSuscription(form);
          }

          fetchSuscriptions();

          hideModal();
          setLoading(false);
          handleReset();
          message.success(messages.default.success);
        } catch (error) {
          setLoading(false);
          handleReset();
          message.error(error.message);
        }
      }
    });
  };

  const onChange = value => {
    // console.log(`selected ${value}`);
  };

  const onBlur = () => {
    // console.log('blur');
  };

  const onFocus = () => {
    // console.log('focus');
  };

  const onSearch = val => {
    // console.log('search:', val);
  };

  const hideModalCmp = val => {
    handleReset();
    hideModal();
  };

  return (
    <Modal
      centered
      width={600}
      title={suscription && suscription.id ? messages.subscription.edit : messages.subscription.add}
      visible={visible}
      onOk={hideModalCmp}
      onCancel={hideModalCmp}
      footer={[
        <Button key="back" onClick={hideModalCmp}>
          {messages.default.cancel}
        </Button>,
        <Button key="submit" type="primary" loading={loading} onClick={submit}>
          {suscription && suscription.id ? messages.default.save : messages.default.create}
        </Button>,
      ]}
    >
      <Form {...formItemLayout} onSubmit={submit} autocomplete="off">
        <Row justify="center" type="flex">
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.title} hasFeedback>
              {getFieldDecorator('title', {
                initialValue: suscription && suscription.title ? suscription.title : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.title.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.category} hasFeedback>
              {getFieldDecorator('CategoryId', {
                initialValue: suscription && suscription.CategoryId ? suscription.CategoryId : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.category.required,
                  },
                ],
              })(
                <Select
                  showSearch
                  placeholder="Select a category"
                  optionFilterProp="children"
                  onChange={onChange}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onSearch={onSearch}
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {categories.map(item => {
                    return <Option key={item.id} value={item.id}>{item.name}</Option>;
                  })}
                </Select>
              )}
            </FormItem>
          </Col>
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.period} hasFeedback>
              {getFieldDecorator('period', {
                initialValue: 1,
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.period.required,
                  },
                ],
              })(
                <Select
                  showSearch
                  placeholder="Select a period"
                  optionFilterProp="children"
                  onChange={onChange}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onSearch={onSearch}
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  <Option value={1}>Month</Option>
                  <Option value={3}>Quarter</Option>
                  <Option value={6}>6 Month</Option>
                  <Option value={12}>Annual</Option>
                  <Option value={24}>Biannual</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.pages} hasFeedback>
              {getFieldDecorator('pages', {
                initialValue: suscription && suscription.pages ? suscription.pages : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.pages.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.freePages} hasFeedback>
              {getFieldDecorator('freePages', {
                initialValue: suscription && suscription.freePages ? suscription.freePages : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.freePages.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.price} hasFeedback>
              {getFieldDecorator('price', {
                initialValue: suscription && suscription.price ? suscription.price : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.price.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.minPrice} hasFeedback>
              {getFieldDecorator('minPrice', {
                initialValue: suscription && suscription.minPrice ? suscription.minPrice : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.minPrice.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.pricePerAdditionalPage} hasFeedback>
              {getFieldDecorator('pricePerAdditionalPage', {
                initialValue:
                  suscription && suscription.pricePerAdditionalPage
                    ? suscription.pricePerAdditionalPage
                    : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.pricePerAdditionalPage.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.freeDays} hasFeedback>
              {getFieldDecorator('freeDays', {
                initialValue: suscription && suscription.freeDays ? suscription.freeDays : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.freeDays.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxSharedEngines} hasFeedback>
              {getFieldDecorator('maxSharedEngines', {
                initialValue:
                  suscription && suscription.maxSharedEngines ? suscription.maxSharedEngines : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxSharedEngines.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxPrivateInstances} hasFeedback>
              {getFieldDecorator('maxPrivateInstances', {
                initialValue:
                  suscription && suscription.maxPrivateInstances
                    ? suscription.maxPrivateInstances
                    : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxPrivateInstances.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxUsers} hasFeedback>
              {getFieldDecorator('maxUsers', {
                initialValue: suscription && suscription.maxUsers ? suscription.maxUsers : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxUsers.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxClones} hasFeedback>
              {getFieldDecorator('maxClones', {
                initialValue: suscription && suscription.maxClones ? suscription.maxClones : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxClones.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canUsePGB}>
              {getFieldDecorator('canUsePGB', {})(
                <Switch
                  onChange={() => {
                    suscription.canUsePGB = !suscription.canUsePGB;
                  }}
                  checked={suscription && suscription.canUsePGB ? suscription.canUsePGB : false}
                />
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canTrain}>
              {getFieldDecorator('canTrain', {})(
                <Switch
                  onChange={() => {
                    suscription.canTrain = !suscription.canTrain;
                  }}
                  checked={suscription && suscription.canTrain ? suscription.canTrain : false}
                />
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canUseApi}>
              {getFieldDecorator('canUseApi', {})(
                <Switch
                  onChange={() => {
                    suscription.canUseApi = !suscription.canUseApi;
                  }}
                  checked={suscription && suscription.canUseApi ? suscription.canUseApi : false}
                />
              )}
            </FormItem>
          </Col>{' '}
          <Col xs={24} md={12}></Col>
        </Row>
      </Form>
    </Modal>
  );
};

const SubscriptionsForm = Form.create()(SubscriptionsFormCmp);

export { SubscriptionsForm };
