import React, { useState, useEffect } from 'react';
import {
  Breadcrumb,
  Card,
  Tabs,
  Row,
  Col,
  Table,
  Button,
  Tooltip,
  Popconfirm,
  message,
  Icon,
  Checkbox,
} from 'antd';
import styled from 'styled-components';
import { CategoryForm } from './categoryForm';
import { SubscriptionsForm } from './subscriptionsForm';
import {
  getCategories,
  delCategory,
  getSuscriptions,
  delSuscription,
  saveSuscription,
} from './../../services';
const { TabPane } = Tabs;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

const Subscriptions = props => {
  const { messages } = props;

  const [category, setCategory] = useState(null);
  const [suscription, setSuscription] = useState({
    canUsePGB: false,
    canTrain: false,
    canUseApi: false,
  });
  const [loading, setLoading] = useState(false);
  const [loadingCat, setLoadingCat] = useState(false);
  const [loadingMul, setLoadingMul] = useState([]);
  const [tab, setTab] = useState('subscription');
  const [visible, setVisible] = useState(false);
  const [categories, setCategories] = useState([]);
  const [suscriptions, setSuscriptions] = useState([]);

  useEffect(() => {
    setLoading(true);
    fetchSuscriptions();
    fetchCategories();
  }, []);

  const operations = (
    <Button
      loading={loading}
      onClick={() => {
        setVisible(true);
      }}
      type="primary"
      icon="plus"
    >
      {tab === 'subscription' ? messages.subscription.add : messages.subscription.addCat}
    </Button>
  );

  const changeTab = key => {
    setTab(key);
  };

  const hideModal = () => {
    setSuscription({
      canUsePGB: false,
      canTrain: false,
      canUseApi: false,
    });
    setCategory(null);
    setVisible(false);
  };

  const editCat = record => {
    setCategory(record);
    setVisible(true);
  };

  const editSusc = record => {
    setSuscription(record);
    setVisible(true);
  };

  const fetchCategories = async () => {
    try {
      const response = await getCategories();
      setCategories(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const removeCategory = async id => {
    try {
      setLoadingCat(id);
      await delCategory(id);
      await fetchCategories();
      setLoadingCat(false);
    } catch (error) {
      message.error(error, message);
      setLoadingCat(false);
    }
  };

  const fetchSuscriptions = async () => {
    try {
      const response = await getSuscriptions();
      setSuscriptions(response.data);
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  const removeSuscription = async id => {
    try {
      setLoadingCat(id);
      await delSuscription(id);

      setLoadingCat(false);
    } catch (error) {
      message.error(error, message);
      setLoadingCat(false);
    }
  };

  const addLoading = value => {
    if (!existLoading(value)) {
      loadingMul.push(value);
      setLoadingMul(loadingMul);
    }
  };

  const delLoading = value => {
    const nl = loadingMul.filter(item => item !== value);
    setLoadingMul(nl);
  };

  const existLoading = value => {
    return loadingMul.includes(value);
  };

  const onChangeRec = async suscription => {
    const aux = `recomended${suscription.id}`;
    try {
      suscription.recomended = !suscription.recomended;
      addLoading(aux);
      const { data } = await saveSuscription(suscription);
      await fetchSuscriptions();
      await delLoading(`recomended${data.id}`);
    } catch (error) {
      message.error(error.message);
      await delLoading(aux);
    }
  };
  const onChangeAct = async suscription => {
    const aux = `active${suscription.id}`;
    try {
      suscription.active = !suscription.active;
      addLoading(aux);
      const { data } = await saveSuscription(suscription);
      await fetchSuscriptions();
      await delLoading(`active${data.id}`);
    } catch (error) {
      message.error(error.message);
      await delLoading(aux);
    }
  };

  const cat_columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      key: 'action',
      width: 230,
      fixed: 'right',
      render: (text, record) => (
        <Row type="flex" justify="end">
          <Tooltip placement="top" title={messages.subscription.editCat}>
            <ButtonAction
              onClick={() => {
                editCat(record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
          </Tooltip>

          <Tooltip placement="top" title={messages.subscription.removeCat}>
            <Popconfirm
              onConfirm={() => {
                removeCategory(record.id);
              }}
              title={messages.subscription.confirmDeleteCat}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                loading={parseInt(loadingCat) === parseInt(record.id)}
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </Tooltip>
        </Row>
      ),
    },
  ];

  // CategoryId: values.CategoryId,

  // maxUsers: values.maxUsers,
  // maxClones: values.maxClones,
  // canTrain: values.canTrain || false,
  // canUseApi: values.canUseApi || false,
  const sus_columns = [
    {
      title: 'Title',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Category',
      dataIndex: 'CategoryId',
      key: 'category',
      render: text => {
        const cat = categories.filter(item => {
          return parseInt(item.id) === parseInt(text);
        });
        if (cat && cat.length) return cat[0].name;
        return text;
      },
    },
    {
      title: 'Max Pages',
      dataIndex: 'pages',
      key: 'pages',
    },
    {
      title: 'Price Per Additional Page',
      dataIndex: 'pricePerAdditionalPage',
      key: 'pricePerAdditionalPage',
    },
    {
      title: 'Free Pages/Days',
      dataIndex: 'freePages',
      key: 'freePages',
      render: (text, record) => {
        return `${record.freePages}/${record.freeDays}`;
      },
    },
    {
      title: 'Price',
      dataIndex: 'price',
      key: 'price',
    },
    {
      title: 'Min Price',
      dataIndex: 'minPrice',
      key: 'minPrice',
    },

    {
      title: 'Period (Month)',
      dataIndex: 'period',
      key: 'period',
    },
    {
      title: 'MaxEngines Shared/Private',
      dataIndex: 'maxSharedEngines',
      key: 'maxSharedEngines',
      render: (text, record) => {
        return `${record.maxSharedEngines}/${record.maxPrivateInstances}`;
      },
    },
    {
      title: 'Max Users',
      dataIndex: 'maxUsers',
      key: 'maxUsers',
    },
    {
      title: 'Max Clones',
      dataIndex: 'maxClones',
      key: 'maxClones',
    },
    {
      title: 'CanUsePGB',
      dataIndex: 'canUsePGB',
      key: 'canUsePGB',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: 'Can Train',
      dataIndex: 'canTrain',
      key: 'canTrain',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: 'Can Use Api',
      dataIndex: 'canUseApi',
      key: 'canUseApi',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: messages.subscription.recomended,
      dataIndex: 'recomended',
      key: 'recomended',
      render: (text, record) => {
        if (existLoading(`recomended${record.id}`)) {
          return <Icon type="loading" />;
        }
        return (
          <Checkbox
            onChange={() => {
              onChangeRec(record);
            }}
            checked={text}
          ></Checkbox>
        );
      },
    },
    {
      title: messages.subscription.active,
      dataIndex: 'active',
      key: 'active',
      render: (text, record) => {
        if (existLoading(`active${record.id}`)) {
          return <Icon type="loading" />;
        }
        return (
          <Checkbox
            onChange={() => {
              onChangeAct(record);
            }}
            checked={text}
          ></Checkbox>
        );
      },
    },
    {
      key: 'action',
      width: 100,
      fixed: 'right',
      render: (text, record) => (
        <Row type="flex" justify="end">
          <Tooltip placement="top" title={messages.subscription.editCat}>
            <ButtonAction
              onClick={() => {
                editSusc(record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
          </Tooltip>
          <Tooltip placement="top" title={messages.subscription.removeCat}>
            <Popconfirm
              onConfirm={() => {
                removeSuscription(record.id);
              }}
              title={messages.subscription.confirmDeleteSus}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                loading={parseInt(loadingCat) === parseInt(record.id)}
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </Tooltip>
        </Row>
      ),
    },
  ];

  return (
    <React.Fragment>
      <Breadcrumb style={{ margin: '16px 0' }}>
        <Breadcrumb.Item>{messages.menus.subscription}</Breadcrumb.Item>
      </Breadcrumb>
      <Card>
        <Tabs defaultActiveKey={tab} tabBarExtraContent={operations} onChange={changeTab}>
          <TabPane tab={messages.menus.subscription} key="subscription">
            <Row>
              <Col xs={24}>
                <Table
                  loading={loading}
                  size="small"
                  dataSource={suscriptions}
                  columns={sus_columns}
                  rowKey={record => record.id}
                />
              </Col>
            </Row>
          </TabPane>
          <TabPane tab={messages.subscription.categories} key="categories">
            <Row>
              <Col xs={24}>
                <Table
                  loading={loading}
                  size="small"
                  dataSource={categories}
                  columns={cat_columns}
                  rowKey={record => record.id}
                />
              </Col>
            </Row>
          </TabPane>
        </Tabs>

        <CategoryForm
          category={category}
          messages={messages}
          visible={visible && tab === 'categories'}
          hideModal={hideModal}
          fetchCategories={fetchCategories}
        />

        <SubscriptionsForm
          suscription={suscription}
          categories={categories}
          messages={messages}
          visible={visible && tab === 'subscription'}
          hideModal={hideModal}
          fetchSuscriptions={fetchSuscriptions}
        />
      </Card>
    </React.Fragment>
  );
};

export default Subscriptions;
